.head {
    padding: 25px 0;
    background-color: #4caf50; }

.navList {
    display: flex;
    justify-content: space-around;
    padding: 0 15px; }

.list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 0;
    & > li {
        padding: 0 15px;
        text-transform: uppercase;
        font-size: 15px;
        letter-spacing: 0.1em;
        justify-content: center;
        font-weight: 500; } }

.link {
    text-decoration: none;
    color: white;
    padding: 10px;
    border: 1px solid;
    &:hover {
        color: red; } }

.headLogo {
    font-size: 48px;
    position: relative;
    color: white;
    bottom: 0.5rem;
    text-align: center; }

.link:focus {
    outline: 0;
    outline: none; }
